:root {
}

* {
}

body {
	margin: 0;
	padding: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
	'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
	sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	font-size: 1.2rem;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
	monospace;
}

.App {
}

nav {
	background-color: #282c34;
	height: min-content;
	color: white;

}

.nav-box {
	width: max-content;
	padding: 2rem;
	margin: 0 auto;

}

.nav-top {
	font-size: 1.8rem;
	font-weight: 600;

}

.webname, .nav-button {
	color: white;
	text-decoration: none;
}

.nav-bottom {
	font-size: 1rem;
	font-weight: 600;
	padding: 10px 0 0 0;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
}

.nav-icons {
	display: flex;
	align-items: center;
}

.nav-button-icon {
	padding: 0 0 0 15px;
	width: 1.5rem;
}

.content {
	background-color: white;
	margin: auto;
	padding: 20px;
	max-width: 720px;
	line-height: 1.6;
	text-align: justify;
}
